import { VersionNumber } from '../JsApiInternalContract';
import * as Enums from './Enums';

export interface Model {}

export interface FilterableModel extends Model {
  // Used for event models that should be filtered on the client side.
  // This prevents extensions from seeing events relevant only to another extension
  // (for example, a settings changed event).  Note: this does not protect against
  // an extension modifying our library and seeing events for other extensions, so do
  // not treat this as a security measure.  If there are events that must be protected
  // from leaking between extensions, that must be done at the c++ layer.
  filterByLocator: boolean;
  locator: ExtensionLocator;
}

export interface FilterableByDialogModel extends FilterableModel {
  filterByDialog: boolean;
  targetDialogUrl?: string;
}

