//// ==================================================================================================================
//// Data Tables
//// ==================================================================================================================

export interface DataValue {
  // These are optional because of ShowDataTableFormat options
  /* tslint:disable:no-any */
  value?: any;
  /* tslint:enable:no-any */
  formattedValue?: string;
  aliasedValue?: string;
  hasAlias?: boolean;
}

export interface DataTable {
  dataTable: Array<Array<DataValue>>;
  headers: Array<DataHeader>;
  marks?: Array<MarkInfo>;
}

export interface DataTableReader {
  id: string;
  totalRowCount: number;
}

export interface MarkInfo {
  type: Enums.MarkType;
  color: string;
  tupleId?: number;
}

export interface DataHeader {
  dataType: Enums.DataType;
  fieldName: string;
  isReferenced: boolean;
  index: number;
  fieldCaption: string;
}

export interface UnderlyingDataTable {
  data: DataTable;
  isSummary: boolean;
}

export interface SelectedMarksTable {
  // If marks are selected across panes, there may be multiple tables of data representing the marks
  data: Array<DataTable>;
}

export interface HighlightedMarksTable {
  // If marks are highlighted across panes, there may be multiple tables of data representing the marks
  data: Array<DataTable>;
}

